import React, { Component, useEffect } from 'react';
import axios from "axios";
import cookie from 'react-cookies';
import Swal from 'sweetalert2'
import { Button, Container, Row, Col } from 'react-bootstrap';
import { MDBContainer, MDBRow, MDBCol } from "mdbreact";

class seating extends React.Component {
  
    constructor(props) {
      super(props);
        this.state = {
        seat: [],
        seat_top : [],
        seat_top2 : [],
        seat_middle : [],
        seat_middle2 : [],
        seat_bottom : [],
        seat_bottom2 : [],
        seatAvailable: [],
        seatReserved: [],
      }
    }

    componentDidMount() {
      this.loadSeat();
    }

    loadSeat = async () => {
      axios.post('/api/LoginForm?type=SeatOut')
      .then((response) => {this.setState({ seat : response.data.json })
      // {this.setState({ seat : response.data.json })
    
      this.setState({ seat_top : this.state.seat.filter(res => res.id <= 6)})
      this.setState({ seat_top2 : this.state.seat.filter(res => res.id >= 7)})
    })
    .catch(console.log)
    }

    onClickData(seat) {
    
      var name = sessionStorage.getItem('username');
      let update_flag = 0;

      for(var l=0; l<this.state.seat.length ; l++){
        if(this.state.seat[l].name == name){
          axios.post('/api/LoginForm?type=Seatupdate', {
            is_Name: name,
            is_Seat: seat
        })
        .then(response => console.log(response.data.json))
        .then(update_flag = 1)
        break;
        }
      }

    if(update_flag == 0){                                                                                                                                                                                                                                     
      axios.post('/api/LoginForm?type=Seatinput', {
        is_Name: name,
        is_Seat: seat
    })
    .then(response => console.log(response.data.json))
    }
    

    axios.post('/api/LoginForm?type=SeatOut')
    .then((response) => {this.setState({ seat : response.data.json }, ()=>{this.loadSeat();})})
    }

    render() {
      console.log('지금이니!');
      return (
        <div>
          <DrawGrid 
            seat = { this.state.seat }
            seat_top = {this.state.seat_top}
            seat_top2 = {this.state.seat_top2}
            available = { this.state.seatAvailable }
            reserved = { this.state.seatReserved }
            onClickData = { this.onClickData.bind(this) }
            />
        </div>
      )
    }
  }
  
  class DrawGrid extends React.Component {

    rendering=(name)=> {
      let result = 0;
      
      if(name != null){
        result = 1;
      }
      return result;
    }

    render() {
      return (
        <div id="image">
            <img src={require("../img/main/layout.png")}alt="" />
              {this.props.seat.map(row =>
                <button class={("button" + row.id)}   type = "button" onClick={e => this.onClickSeat(row.id)}>{row.name}</button>)}
        </div>
         /* <MDBCol md="6">
          <MDBRow>
            { this.props.seat_top.map( row =>
           <MDBCol
              //  className={this.rendering(row.name) > 0? 'reserved': 'available'}
                 key={row} 
                 onClick = {e => this.onClickSeat(row.id)}>  
                 {row.name}
                 <img src={require("../img/main/layout/layout" + row.id + ".png")} alt="" />
           </MDBCol>) }
           </MDBRow>
           <MDBRow>
               { this.props.seat_top2.map( row =>
               <MDBCol
              //  className={this.rendering(row.name) > 0? 'reserved': 'available'}
                 key={row} 
                 onClick = {e => this.onClickSeat(row.id)}>  
                 {row.name}
                 
                 <img src={require("../img/main/layout/layout" + row.id + ".png")} alt="" />
              </MDBCol>) }
              </MDBRow>
              </MDBCol> */


           
           

         /* <Row>
           <Col sm={6}>
            <img src={require("../img/main/layout/layoutF1.PNG")} alt="" />
           </Col>
           { this.props.seat_top2.map( row =>
             <Col 
              //  className={this.rendering(row.name) > 0? 'reserved': 'available'}
                 key={row} 
                 onClick = {e => this.onClickSeat(row.id)}>  
                 <img src={require("../img/main/layout/layout" + row.id + ".png")} alt="" />
                 {row.name}
               </Col>) }
         </Row> */

         /* <AvailableList available = { this.props.available } />
         <ReservedList reserved = { this.props.reserved } /> */ 
     )
    }
    
    onClickSeat(seat) {
      this.props.onClickData(seat);
  }
  }
  
  class AvailableList extends React.Component {
    render() {
      const seatCount = this.props.available.length;
      return(
        <div className="left">
          <h4>Available Seats: ({seatCount == 0? 'No seats available' : seatCount})</h4>
          <ul>
            {this.props.available.map( res => <li key={res} >{res}</li> )}
          </ul>
        </div>
      )
    }
  }
  
  class ReservedList extends React.Component {
    render() {
      return(
        <div className="right">
          <h4>Reserved Seats: ({this.props.reserved.length})</h4>
            { this.props.reserved.map(row =>
                <td 
                key={row}>
                  {row.id}
                 </td>) }
        </div>
      )
    }
  }
  export default seating;

